import "@Styles/components/GameRules.scss";
import React, { useState } from "react";
const GameRules = () => {
  const [expandedSections, setExpandedSections] = useState({});

  const toggleSection = (section) => {
    setExpandedSections((prevState) => ({
      ...prevState,
      [section]: !prevState[section],
    }));
  };

  return (
    <div className="game-rules">
      <h2
        className="primary-button bg-orange show-rules"
        onClick={() => toggleSection("showRules")}
      >
        Rgles du Jeu
      </h2>

      <div
        className={`rules-paragraph ${expandedSections.showRules && "show"}`}
      >
        <h3>Objectif</h3>
        <p>
          L'objectif est d'atteindre la destination de vacances. Mais attention,
          tous le monde convoite la meilleure chambre avec balcon et vue sur la
          mer ! <br />
          Vous devez donc tre rapide et stratgique afin de surmonter les
          piges qui vous feront face et ralentissant vos adversaires.
        </p>

        <h3>Droulement du Jeu</h3>

        <p>
          Chaque joueur reoit un certain nombre de cartes au dbut du jeu.
          <br />  chaque tour, les joueurs jouent une carte de leur main, puis
          piochent une nouvelle carte. <br />
          Les joueurs peuvent utiliser des cartes pour avancer, se protger,
          s'immuniser ou attaquer. <br />
          Le premier joueur  atteindre ladestination de vacances gagne la
          partie.
        </p>

        <h3>Cartes du Jeu</h3>
        <ul>
          <li>
            <h4 className="color-sky">Cartes de Kilomtres</h4>
            <p>
              Ces cartes vous permettent d'avancer d'un certain nombre de
              kilomtres.
            </p>
          </li>
          <li>
            <h4 className="color-green">Cartes de protection</h4>
            <p>Protgez-vous contre les attaques des autres joueurs.</p>
          </li>
          <li>
            <h4 className="color-red">Cartes d'attaque</h4>
            <p>Utilisez ces cartes pour ralentir ou arrter vos adversaires.</p>
          </li>
          <li>
            <h4 className="color-orange">Cartes bonus</h4>
            <p>
              Elles vous permettent d'tre immunis contre certaines attaques.
            </p>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default GameRules;
